<template>
  <el-dialog
    title="ÁºñËæëÂºÇÂ∏∏Áä∂ÊÄÅ"
    :visible="visible"
    width="400px"
    :before-close="handleClose"
    @close="handleClose"
  >
    <div class="status-edit-form">
      <el-form :model="form" label-width="80px">
        <el-form-item label="ÂΩìÂâçÁä∂ÊÄÅ">
          <el-tag :type="getStatusTagType(currentStatus)">
            {{ getStatusText(currentStatus) }}
          </el-tag>
        </el-form-item>
        
        <el-form-item label="Êñ∞Áä∂ÊÄÅ" required>
          <el-select v-model="form.newStatus" placeholder="ËØ∑ÈÄâÊã©Êñ∞Áä∂ÊÄÅ" style="width: 100%">
            <el-option label="ÂæÖÂ§ÑÁêÜ" value="pending"></el-option>
            <el-option label="Â∑≤Ëß£ÂÜ≥" value="resolved"></el-option>
            <el-option label="Â∑≤ÂøΩÁï•" value="ignored"></el-option>
            <el-option label="Ë∞ÉÊü•‰∏≠" value="investigating"></el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="Â§áÊ≥®">
          <el-input
            v-model="form.notes"
            type="textarea"
            :rows="3"
            placeholder="ÂèØÈÄâÔºöÊ∑ªÂä†Áä∂ÊÄÅÂèòÊõ¥Â§áÊ≥®"
          ></el-input>
        </el-form-item>
      </el-form>
    </div>
    
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose">ÂèñÊ∂à</el-button>
      <el-button type="primary" @click="handleConfirm" :loading="loading">
        Á°ÆÂÆö
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'AbnormalStatusEditDialog',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    equipSn: {
      type: String,
      default: ''
    },
    currentStatus: {
      type: String,
      default: 'pending'
    }
  },
  data() {
    return {
      form: {
        newStatus: '',
        notes: ''
      },
      loading: false
    }
  },
  watch: {
    visible(val) {
      if (val) {
        this.form.newStatus = this.currentStatus
        this.form.notes = ''
      }
    }
  },
  methods: {
    handleClose() {
      this.$emit('close')
      this.resetForm()
    },
    
    resetForm() {
      this.form.newStatus = ''
      this.form.notes = ''
      this.loading = false
    },
    
    async handleConfirm() {
      if (!this.form.newStatus) {
        this.$notify.warning({
          title:'ÊèêÁ§∫',
          message:'ËØ∑ÈÄâÊã©Êñ∞Áä∂ÊÄÅ'
        })
        return
      }
      
      this.loading = true
      try {
        await this.$api.equipment.updateAbnormalEquipmentStatus(this.equipSn, {
          status: this.form.newStatus,
          notes: this.form.notes
        })
        
        this.$notify.success({
          title:'ÊèêÁ§∫',
          message:`ÂºÇÂ∏∏Áä∂ÊÄÅÂ∑≤Êõ¥Êñ∞‰∏∫: ${this.getStatusText(this.form.newStatus)}`
        })
        this.$emit('success', {
          equipSn: this.equipSn,
          newStatus: this.form.newStatus,
          notes: this.form.notes
        })
        this.$emit('close')
        this.resetForm()
      } catch (error) {
        console.error('Êõ¥Êñ∞Áä∂ÊÄÅÂ§±Ë¥•:', error)
        this.$notify.error({
          title:'ÊèêÁ§∫',
          message:'Êõ¥Êñ∞Áä∂ÊÄÅÂ§±Ë¥•'
        })
      } finally {
        this.loading = false
      }
    },
    
    getStatusTagType(status) {
      switch (status) {
        case 'pending':
          return 'warning'
        case 'resolved':
          return 'success'
        case 'ignored':
          return 'info'
        case 'investigating':
          return 'danger'
        default:
          return 'info'
      }
    },
    
    getStatusText(status) {
      switch (status) {
        case 'pending':
          return 'ÂæÖÂ§ÑÁêÜ'
        case 'resolved':
          return 'Â∑≤Ëß£ÂÜ≥'
        case 'ignored':
          return 'Â∑≤ÂøΩÁï•'
        case 'investigating':
          return 'Ë∞ÉÊü•‰∏≠'
        default:
          return 'Êú™Áü•'
      }
    }
  }
}
</script>

<style scoped>
.status-edit-form {
  padding: 20px 0;
}

.dialog-footer {
  text-align: right;
}
</style> 